import {pokemonApi} from "../api/api";

const SET_CARDS = 'SET_CARDS';


let init_state = {
    page: 1,
    pageSize: 4,
    cards: [],
};
const pokemonCardsReducer = (state = init_state, action) => {
    switch (action.type) {
        case SET_CARDS:
            return {
                ...state, cards: [...action.cards],
            };
        default:
            return state;
    }
}

export const setCards = (cards) => {
    return {type: SET_CARDS, cards}
}

export const getPokemonCards = (page, pageSize) => {
    return async (dispatch) => {
        let response = await pokemonApi.getPortionCards(page, pageSize);
        console.log(response);
        dispatch(setCards(response.data));
    }
}

export default pokemonCardsReducer;