import Navbar from "../Navbar/Navbar";
import css from './PokemonCards.module.css';
import Card from "./Card/Card";
import React, {useState} from 'react';
import {authApi} from "../../api/api";
import PokemonCards from "./PokemonCards";
import {connect} from "react-redux";
import {getPokemonCards} from "../../redux/pokemon-cards-reducer";

class PokemonCardsContainer extends React.Component {
    componentDidMount() {
        this.props.getPokemonCards(this.props.page, this.props.pageSize);
    }

    render() {
        return <PokemonCards {...this.props} />
    }
}
let mapStateToProps = (state) => {
    return {
        page: state.pokemonCards.page,
        pageSize: state.pokemonCards.pageSize,
        cards: state.pokemonCards.cards,
    };
}

export default connect(mapStateToProps, {getPokemonCards})(PokemonCardsContainer);